var listsNested = document.querySelectorAll('.paragraph-list__lists_nested');
var listsText = document.querySelectorAll('.paragraph-list__text-link');
var lastClickElement;
var resolutionForClick = true;

function hiddenLists() {
  for(var i = 0; i < listsNested.length; i++) {
    listsNested[i].classList.remove('show_flex');
  }
  for(var i = 0; i < listsText.length; i++) {
    listsText[i].classList.remove('paragraph-list__text-link_active');
  }
}

function openLists(e){
  var element;
  if(!e.type){
    element = e;
  } else {
    element = e.target;
  }
  if(element.classList.contains('paragraph-list__text-link_arrow')) {
    element.parentNode.querySelector('.paragraph-list__lists_nested').classList.toggle('show_flex');
  }
  element.classList.toggle('paragraph-list__text-link_active');
}

function openHeader(burger){
  hiddenLists(); 
  showException();
  burger.classList.toggle('header__burger_active');
  burger.parentNode.classList.toggle('header__wraper_open');
  burger.parentNode.querySelector('.header__container').classList.toggle('header__container_hidden_mob');
}

function mobileOpenHeader(e){
  var click = e.target;
  if(click.classList.contains('header__burger')) {
    var burger = click;
    openHeader(burger);
  }
  if(click.parentNode.classList.contains('header__burger')) {
    var burger = click.parentNode;
    openHeader(burger);
  }
  if(click.classList.contains('paragraph-list__text-link')){
    if(lastClickElement === click) {
      hiddenLists();
      lastClickElement = null;
    } else {
      hiddenLists();
      openLists(e);
      lastClickElement = click;
    }
  }
}

function showException(){
  hiddenActiveLinks();
  for(var i = 0; i < exception.length; i++){
    exception[i].classList.add(exception[i].activeClass);
  }
}

function choiceBehaviorDevice(){
  if(document.documentElement.clientWidth > 770) {
    document.addEventListener('mouseover', function(e) {
      if(e.target.classList.contains('paragraph-list__text-link')) {
        hiddenLists();   
        openLists(e);
        
      }
      if(e.target.classList.contains('container__display-mob') || e.target.classList.contains('logo')){
        hiddenLists();
        showException();
      }
    });
  } else {
    document.addEventListener('touchstart', function(e) {
      if(resolutionForClick) {
        if(e.target.classList.contains('container__display-mob')){
          openHeader(document.querySelector('.header__burger'));
        }
        resolutionForClick = false;
        mobileOpenHeader(e);
        setTimeout(function(){
          resolutionForClick = true;
        }, 150)
      }
    });
    document.addEventListener('click', function(e) {
      if(resolutionForClick) {
        if(e.target.classList.contains('container__display-mob')){
          openHeader(document.querySelector('.header__burger'));
        }
        resolutionForClick = false;
        mobileOpenHeader(e);
        setTimeout(function(){
          resolutionForClick = true;
        }, 150)
      }
    });
  }
}
choiceBehaviorDevice();

window.addEventListener('resize', function(){
  choiceBehaviorDevice();
});

var links = document.querySelectorAll('.paragraph-list__link');
var exception = [];

function searchParentNode(element, nameClass){
  if(element.classList.contains(nameClass)){
      return element;
  } else {
    return searchParentNode(element.parentNode, nameClass);
  }
}

function hiddenActiveLinks(){
  for(var j = 0; j < links.length; j++){
    links[j].classList.remove('paragraph-list__link_active');
  }
}

function checkMouseOverElement(e, element){
  var elementX = element.getBoundingClientRect().left;
  var elementY = element.getBoundingClientRect().top;
  var elementWidth = element.getBoundingClientRect().width;
  var elementHeight = element.getBoundingClientRect().height;
  if(!(e.pageX >= elementX && e.pageX <= elementX + elementWidth && e.pageY >= elementY && e.pageY <= elementY + elementHeight)) {
    return true;
  } else {
    return false;
  }  
}

for(var i = 0; i < links.length; i++){
  links[i].addEventListener('mouseover', function(){
    hiddenActiveLinks();
    this.classList.add('paragraph-list__link_active');
  });
  for(var j = 0; j < listsNested.length; j++) {
    listsNested[j].addEventListener('mouseout', function(e){
      if(checkMouseOverElement(e, this)) {
        hiddenLists();  
        showException();
      }
    });
  }  
  if(links[i].classList.contains('paragraph-list__link_active')){
    var parentLinks = searchParentNode(links[i], 'lists__paragraph-list_parent');
    var parentLinksNested = parentLinks.querySelector('.paragraph-list__lists_nested');
    var parentLinksTextLink = parentLinks.querySelector('.paragraph-list__text-link');
    hiddenLists();   
    openLists(parentLinksTextLink);
    exception = [];
    exception.push(links[i]);
    links[i].activeClass = 'paragraph-list__link_active';
    exception.push(parentLinksNested);
    parentLinksNested.activeClass = 'show_flex';
    exception.push(parentLinksTextLink);
    parentLinksTextLink.activeClass = 'paragraph-list__text-link_active';
  }
}
